import * as React from "react";
import styled from "styled-components";

const Container = styled.div`
  float: left;
  width: ${p => (p.size / 4) * 100}%;
  padding: 5px;
  margin-bottom: 5px;
  button {
    :focus {
      outline: none;
    }
    :hover {
      background: #eee;
    }
    :active {
      box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.4);
    }
    color: #000;
    font-size: 20px;
    line-height: 30px;
    text-align: right;
    cursor: pointer;
    width: 100%;
    background: transparent;
    border-radius: 5px;
    border: 2px solid #000;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);

    &[data-color="gray"] {
      background: #eee;
      :hover {
        background: #ccc;
      }
    }
  }
`;
// font-weight: bold; line-height: 60px;
class Button extends React.Component {
  constructor(props){
    super(props);

    this.clickFomulaButton = this.clickFomulaButton.bind(this);
  }

  clickFomulaButton(data) {
     return new Function(this.setStatus({ displayValue: data }))();
  }

  listOfButtons(){
    let listOfData = this.props.formular;
    return listOfData.map((data, i) => {
      return <button 
      key={data}
      onClick={event => this.clickFomulaButton(data)}>
        {data.formula}
        <br/>
        {"=" + data.result}
        <span>{i}</span>
      </button>
    })
  }

  render() {
    const { size = 4, color, children } = this.props;
    return (
      <Container size={size}>
        {this.listOfButtons()}
      </Container>
    );
  }
}

export default Button;
